import React, { useState, useEffect } from 'react';
import Form from './components/Form';
import PaymentModal from './components/PaymentModal';
import AnalysisSection from './components/AnalysisSection';
import './App.css';
import { createOrderStream, initiatePayment } from './api';

const ANALYSIS_TYPES = [
  { id: 'health', name: 'å¥åº·åˆ†æ' },
  { id: 'career', name: 'äº‹ä¸šåˆ†æ' },
  { id: 'wealth', name: 'è´¢è¿åˆ†æ' },
  { id: 'marriage', name: 'å©šå§»åˆ†æ' }
];

function App() {
  const [currentOrderId, setCurrentOrderId] = useState(null);
  const [analysisData, setAnalysisData] = useState({
    preview: '',
    unlocked: {},
    contents: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    }
  });
  const [paymentState, setPaymentState] = useState({
    show: false,
    qrcodeUrl: '',
    processingType: null
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [es, setES] = useState(null);

  const handleSubmit = async (eightCharacters) => {

    setLoading(true);
    setError('');
    setAnalysisData({
      preview: '',
      unlocked: {},
      contents: { health: '', career: '', wealth: '', marriage: '' }
    });

    const newES = createOrderStream(eightCharacters, {
      onStream: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          preview: prev.preview + chunk
        }));
        setLoading(false);
      },
      onComplete: (prevOrderId) => {
        setCurrentOrderId(prevOrderId ); // æ¨¡æ‹Ÿè®¢å•ID
      },
      onError: (message) => {
        setError(message || 'ç”Ÿæˆé¢„è§ˆå¤±è´¥');
        setLoading(false);
      }
    });
    setES(newES);
  };
  const unlockAnalysis = (type) => {
    if (!currentOrderId) return;
    console.log("é€‰æ‹©åˆ†æ",type)
    setPaymentState({
      show: true,
      qrcodeUrl: '',
      processingType: type
    });

    const paymentES = initiatePayment(currentOrderId, type, {
      onPaymentQR: (qrcodeUrl, orderId) => {
        setPaymentState(prev => ({ ...prev, qrcodeUrl }));
        // setCurrentOrderId(orderId);
      },
      onAnalysisChunk: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          contents: {
            ...prev.contents,
            [type]: (prev.contents[type] || '') + chunk
          }
        }));
      },

      onPaymentstatus: () => {
        setAnalysisData(prev => ({
          ...prev,
          unlocked: { ...prev.unlocked, [type]: true }
        }));
        setPaymentState({ show: false, qrcodeUrl: '', processingType: null });
      },

      onAnalysisEnd: () => {
        // setAnalysisData(prev => ({
        //   ...prev,
        //   unlocked: { ...prev.unlocked, [type]: true }
        // }));
        // setPaymentState({ show: false, qrcodeUrl: '', processingType: null });
      },

      onError: (message) => {
        setError(message || 'æ”¯ä»˜æµç¨‹å‡ºé”™');
        setPaymentState({ show: false, qrcodeUrl: '', processingType: null });
      }
    });

    setES(paymentES);
  };

  useEffect(() => {
    return () => es?.close();
  }, [es]);


  return (
    <div className="App">
      <header className="header">
        <div className="header-content">
          <div className="branding">
            <h1>LiMingå‘½ç†</h1>
            <p className="slogan">ä¸€é˜´ä¸€é˜³ä¹‹è°“é“</p>
          </div>
          <div className="logo-container">
            <img src="/logo.png" alt="Logo" className="logo" />
          </div>
        </div>
      </header>

      
        {error && <div className="error-message">{error}</div>}
        
        {loading ? (
            <div className="loading-wrapper">
            <div className="loading">
              â³ æ·±åº¦åˆ†æé€šå¸¸éœ€è¦ 1~2 åˆ†é’Ÿ
              <div className="sub-hint">ï¼ˆæœŸé—´è¯·ä¸è¦åˆ·æ–°é¡µé¢ï¼‰</div>
            </div>
          </div>
        ) : (
          !analysisData.preview ? (
            <Form onSubmit={handleSubmit} />
          ) : (
            <AnalysisSection 
              preview={analysisData.preview}
              unlocked={analysisData.unlocked}
              contents={analysisData.contents}
              onUnlock={unlockAnalysis}
              analysisTypes={ANALYSIS_TYPES}
            />
          )
        )}

        <PaymentModal
          isOpen={paymentState.show}
          qrcodeUrl={paymentState?.qrcodeUrl}
          onClose={() => setPaymentState({ show: false, qrcode: '', processingType: null })}
        />
    

      <footer>
        <div className="sfooterContent">
          <p className="sdisclaimer">
            æ‹’ç»å‘æœªæˆå¹´äººæä¾›æœåŠ¡ï¼Œä»…ä¾›å¨±ä¹å’Œå‚è€ƒã€‚
          </p>
          <p className="sdisclaimer">
          Â©2025 LiMingå‘½ç†
          </p>
        </div>
      </footer>

    </div>
    
  );
}

export default App;




